export enum reservationStatus {
  new = 'Создана', // Создана, не одобрена и не подтверждена
  approved = 'Одобрена', // Одобрена управляющим
  confirmed = 'Подтверждена', // Подтверждена
  closed = 'Закончена', // Была подтверждена, успешно закрыта либо в процессе брони, либо по завершении
  canceled = 'Отменена пользователем', // Отменена пользователем до подтверждения
  denied = 'Отказано в одобрении', // Отказано в одобрении управляющим
  refused = 'Не была одобрена или подтверждена', // Не была одобрена или подтверждена, время подтверждения истекло
  undefined = 'Не определно' // если значение не определено
}
